body {
    background: linear-gradient(black, aqua);
    height: 100vh;
    text-align: center;
    color: white;
    font-weight: bold;
    font-size: 50px;
}
img {
    margin-top: 3.5%;
}
.staple{
    background: linear-gradient(135deg, green 0px, lime 100%);
    margin-right: 15%;
}
.stinker{
    background: linear-gradient(135deg, red 0px, orange 100%);
    margin-left: 15%;
}
.guess{
    height: fit-content;
    width: fit-content;
    font-size: 5vw;
    font-family: Courier;
    border: solid 0.7vh;
    border-radius: 1.4vh;
}
.guess:hover{
    cursor: pointer;
    filter: brightness(50%);
}
.rotation{
    margin-top: 10%;
    font-size: 75px;
    transform: rotate(10deg);
    animation: curve-rotate 2s infinite;
}
.score {
    position:absolute;
    color: black;
    font-size: 3vw;
    bottom:1%;
    left:2%;
}
.highScore{
    position:absolute;
    bottom:1%;
    right:2%;
    color: black;
    font-size: 3vw;
}
.container{
    width: 100%;
    height: 100%;
}
.inspiration{
    position:absolute;
    color: black;
    font-size: 3vw;
    bottom:1%;
    left:2%;
}
.title{
    background: black;
    margin-top: 3.5%;
    font-size: 75px;
}
.green{
    color: green;
}
.red{
    color: red;
}

@keyframes curve-rotate {
    0% {transform: rotate(0deg);}
    25% {transform: rotate(25deg);}
    50% {transform: rotate(0deg);}
    75% {transform: rotate(-25deg);}
    100% {transform: rotate(0deg);}
}